<template>
	<div id="userAddress">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">我的地址</div>
        <div class="van-nav-bar__right" @click="$router.push('addAddress')">
          新增
        </div>
      </div>
    </div>
    <div class="login_box">
      <div class="van-radio addressBox" v-for="(item,index) in addressList" @click="toEditAddress(item)">
        <!--<span class="van-radio__input">-->
          <!--<input type="radio" class="van-radio__control" value="1">-->
          <!--<i class="van-icon van-icon-checked"></i>-->
        <!--</span>-->
        <span class="van-radio__label margin_left_0">
          <div>
            <span>{{item.username}}</span>
            <span class="font_12 color_8b8b8b">{{item.telephone}}</span>
            <span class="font_12 color_8b8b8b" v-if="item.type==1">(默认)</span>
            <br>
            <span class="font_12 addressInfo">{{item.area}}{{item.detailaddress}}</span>
          </div>
        </span>
      </div>
      <!--<van-radio-group v-model="radio">-->
        <!--<van-radio name="1" style="padding: 5px 10px;background-color: #ffffff;margin: 5px 15px;border-radius: 5px;height: 48px;">-->
          <!--<div style="padding-left: 20px;margin-top: -16px;">-->
            <!--<span>刘先生</span>-->
            <!--<span class="font_12 color_8b8b8b">18755554444</span>-->
            <!--<br>-->
            <!--<span class="font_12 addressInfo">奥斯卡见到过滑石粉噶奥斯卡见到过滑石粉噶事进口国和发几首歌事进口国和发几首歌</span>-->
          <!--</div>-->
        <!--</van-radio>-->
        <!--<van-radio name="2" style="padding: 5px 10px;background-color: #ffffff;margin: 5px 15px;border-radius: 5px;height: 48px;">-->
          <!--<div style="padding-left: 20px;margin-top: -16px;">-->
            <!--<span>刘先生</span>-->
            <!--<span class="font_12 color_8b8b8b">18755554444</span>-->
            <!--<br>-->
            <!--<span class="font_12 addressInfo">奥斯卡见到过滑石粉奥斯卡见到过滑石粉噶事进口国和发几首歌噶事进口国和发几首歌</span>-->
          <!--</div>-->
        <!--</van-radio>-->
      <!--</van-radio-group>-->
    </div>
	</div>
</template>
<script>
	export default {
		name: 'userAddress',
		data() {
			return {
        addressList:[],
        userId:localStorage.getItem('userId')
			}
		},
    mounted() {
      this.getAddressList();
    },
		methods:{
      toEditAddress(dataMap){
        this.$router.push({path:'editAddress',query:{data:dataMap}});
      },
      getAddressList(){
        let $this = this;
        let obj = {
          userid: this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("address/getaddressList", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.addressList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style>
  .addressBox {
    padding: 5px 10px;
    background-color: rgb(255, 255, 255);
    margin: 5px 15px;
    border-radius: 5px;
    height: 40px;
  }
  #userAddress .van-radio__input,#userAddress .van-radio__label {
    display: -webkit-box;
    vertical-align: middle;
  }
  .addressInfo {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
    /*width: 63%;*/
    min-width: 63%;
    height: 24px;
    display: block;
  }
  #userAddress .van-radio__input {
    height: 1em;
    font-size: 20px;
    position: relative;
    top: 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



